<script context="module">
  let posts = [];
  let blogInfo = {};

  export function preload() {
    // return fetch('https://sapper-template.now.sh/blog.json')
    // return fetch('http://localhost:5000/blog.json')
    // 	.then(r => r.json())
    // 	.then(arr => {
    // 		posts = arr;
    // 	});
    window.gun.get("headline").on(headLine => {
      blogInfo.title = headLine.title;
      blogInfo.description = headLine.description;
    });
    posts = [];
    window.gun
      .get("posts")
      .map()
      .on(post => {
        posts.push(post);
      });
  }
</script>

<style>
  ul {
    margin: 0 0 1em 0;
    line-height: 1.5;
  }
  .home,
  .blog-title,
  .blog-description {
    max-width: 700px;
    margin: auto;
    text-align: center;
    width: 100%;
  }
  .blog-title {
    font-size: 36px;
  }
</style>

<svelte:head>
  <title>Blog</title>
</svelte:head>

<input class="blog-title" bind:value={blogInfo.title} />
<br />
<input class="blog-description" bind:value={blogInfo.description} />
<h1>Recent posts</h1>

<ul>
  {#each posts as post}
    <li>
      <a href="/blog/{post.id}">{post.title}</a>
    </li>
  {/each}
</ul>
